import request from "@/request/index";

export function getUserInfo() {
    return request.get("/user/person/info");
}

export function updateUserInfo(user) {
    return request.post("/user/person/update", {...user});
}

export function forgetPass(user) {
    return request.post("/user/person/pass/code", {...user});
}

export function resetPass(user) {
    return request.post("/user/person/pass", {...user});
}


export function requestCode(user) {
    return request.post("/user/person/code", {...user});
}

export function login(user){
    return request.post("/user/login",{...user})
}

export function logout() {
    return request.get("/user/logout");
}
